@import url(https://fonts.googleapis.com/css?family=Oxygen:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Montserrat|Poppins|Roboto|Rubik);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* 
orange: #E05915 
purple: #431C5D
white: #E6E9F0
light-blue: #BCCBDE
red: #E01515
*/

html,
body {
    font-size: 10px;
}

a {
    text-decoration: none;
    color: inherit;
}

.cursor {
    cursor: pointer;
}

.btn {
    width: 140px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius:20px; 
    font-size: 2rem;
    color: #ffffff;
    border: 0;
}

.purple {
    background-color:#431C5D;
}

.orange {
    background-color:#E05915;
}

.white {
    background-color: #E6E9F0; 
}

.light-blue {
    background-color: #BCCBDE;
}

.red {
    background-color: #E01515;
}

.pageStructure {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh; 
}

.container {
    width: 960px;
    height: 70vh;
    display: flex;
}

.half-container {
    width: 50%;
    height: 100%;
}

.profile-page-image img {
    height: 302px;
    width: 302px;
    border-radius: 50%;
}

.profile-page-btn {
    display: flex;
}
/* 
orange: #E05915 
purple: #431C5D
*/

nav {
    font-family: 'Oxygen', sans-serif;  
    font-size: 2rem;
    position: fixed;
    width:100vw;
    display: flex;
    justify-content: center;
    background-color: rgba(255,255,255,1)
}

.navbar h2 {
    font-weight: 700;
    color: #E05915;
    font-size: 2.5rem;
}

.navbar ul {
    display: flex;
    align-items: center;
}

.navbar ul li {
    margin-left:32px; 
    color: #431C5D; 
}

.navbar {
    align-items: center; 
    display: flex;
    justify-content: space-between;
    height: 10vh;
    width: 960px;
}

.link-photo-profile {
    display: flex;
    align-items: center;
}

.link-photo-profile  img{
    height: 30px;
    border-radius: 50%;
    margin-left: 8px; 
}

